import React from 'react';
import { MapContainer, TileLayer, Marker, Tooltip } from 'react-leaflet'

const position = [44.91242827800883, 8.192341851030596] // Asti
const style = { height: '600px', maxWidth: '100%' };

function WSMarker(props) {
  const clickHandler = (event, id) => {
    console.log('marker clicked', event)
    window.location.assign('/' + id)
  }

  return (
    <Marker 
      position={position}
      eventHandlers={{
        click: (e) => clickHandler(e, props.id)
      }}>
      <Tooltip>
        Osservatorio meteorologico di ASTI nord, zona Ospedale C. Massaia - 155 m s. l. m.
      </Tooltip>
    </Marker>
  )

}

export default function WSMap(props) {
    if (typeof window !== 'undefined') {
      return (
        <div id="mapid">
          <link rel="stylesheet" href="https://unpkg.com/leaflet@1.7.1/dist/leaflet.css"
                integrity="sha512-xodZBNTC5n17Xt2atTPuE1HxjVMSvLVW9ocqUKLsCC5CXdbqCmblAshOMAS6/keqq/sMZMZ19scR4PsZChSR7A=="
                crossOrigin=""
          />
          <script src="https://unpkg.com/leaflet@1.7.1/dist/leaflet.js"
                  integrity="sha512-XQoYMqMTK8LvdxXYG3nZ448hOEQiglfqkJs1NOQV44cWnUrBc8PkAOcXy20w0vlaXaVUearIOBhiXZ5V3ynxwA=="
                  crossOrigin=""></script>
          
          <MapContainer center={position} zoom={13} scrollWheelZoom={false} style={style}>
            <TileLayer
              attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            <WSMarker id="1" />
          </MapContainer>
        </div>
      )
    }

    return null
}